/*========Nullstyle=========*/
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  scroll-behavior: smooth;
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

/*========Fonts=========*/
@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&display=swap');

/*========Main style=========*/
body {
  &.lock {
    overflow: hidden;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  max-width: 100%;
  width: 100%;
  font-family: 'Ubuntu', sans-serif;
  /*========Header=========*/
  .header {
    .header__container {
      max-width: 100%;
      overflow: hidden;
      //Navigation
      .header__navigation {
        top: 0;
        left: 0;
        width: 100%;
        padding: 30px 50px 30px 30px;
        background: #305a83;
        border-bottom: 1px solid #35689a;
        position: fixed;
        z-index: 100;

        .header__menu {
          display: flex;
          flex-direction: row;
          align-items: center;
          color: white;
          font-size: 30px;
          gap: 30px;

          .menu__item {
            cursor: pointer;

            &.hovered {
              position: relative;

              &:after {
                content: "";
                position: absolute;
                bottom: -5px;
                left: 0;
                height: 2px;
                width: 0;
                background: white;
                transition: width 0.3s ease-in-out;
              }

              &:hover {
                &:after {
                  width: 100%;
                }
              }
            }

            a {
              position: relative;
              color: white;
            }

            &.title {
              font-size: 40px;
              padding: 0 10px 0 0;
              text-transform: uppercase;
              border-right: 2px solid white;
            }

            &.chose-category {
              position: relative;
              height: 30px;
              width: 80px;
              cursor: pointer;

              span {
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
              }

              span, span:after, span:before {
                height: 3px;
                width: 40px;
                position: absolute;
                transition: all 0.3s ease;
                background: white;
              }

              span:after {
                content: "";
                top: 12px;
              }

              span:before {
                content: "";
                top: -12px;
              }

              &.active {

                span {
                  height: 0;
                  background: none;
                }

                span:after {
                  top: 0;
                  transform: rotate(-45deg);
                }

                span:before {
                  top: 0;
                  transform: rotate(45deg);
                }
              }
            }

            &.mobile-search-news {
              display: none;
            }

            &.search-news {
              cursor: auto;
              flex: 1;

              .search-news__form {
                width: 70%;
                position: relative;

                .search-news__input {
                  width: 100%;
                  padding: 10px 40px;
                  border-radius: 30px;
                  font-size: 30px;
                }

                .search-news__button {
                  color: #305a83;
                  position: absolute;
                  top: 0;
                  right: 30px;
                  height: 100%;
                  font-size: 30px;
                  background: none;
                }
              }
            }

            &.change-country {
              justify-self: flex-end;
              padding: 0 0 0 10px;
              border-left: 2px solid white;

              .change-country__select {
                cursor: pointer;
                padding: 0 5px 0 0;
                border-radius: 30px;
                background: rgba(0, 0, 0, 0);
                outline: none;
                color: white;
                font-family: 'Ubuntu', sans-serif;
                font-size: 30px;

                option {
                  color: #305a83;
                }

              }
            }
          }
        }
      }

      //Loader
      .display__loader {
        position: absolute;
        width: 100%;
        left: 0;
        bottom: 0;
        background: #305a83;
        opacity: 1;
        visibility: visible;
        transition: 0.3s all;
        z-index: 90;

        .loader {
          width: 100%;
          height: 6px;
          background: #35689a;
          transform: translate(-100%, 0);
          // animation-play-state: running;
          animation: 1s slide infinite linear;
        }

        &.done {
          opacity: 0;
          visibility: hidden;

          .loader {
            animation: 0s slide infinite linear;
          }
        }

        @keyframes slide {
          from {
            transform: translate(-150%, 0);
          }
          to {
            transform: translate(200%, 0);
          }
        }
      }

      //Pop menu
      .header__category {
        z-index: 100;
        position: fixed;
        top: 115px;
        left: 0;
        height: 100%;
        width: 100%;
        transform: translate(-100%, 0);
        transition: transform 0s ease-in-out 0.3s;

        &:before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: rgba(0, 0, 0, 0.1);
          opacity: 0;
          transition: all 0.3s ease-in-out;
        }

        &.active {
          transform: translate(0, 0);
          transition: transform 0s ease-in-out;

          &:before {
            opacity: 1;
          }

          .category__menu {
            transform: translate(0, 0);
          }
        }

        .category__menu {
          display: flex;
          flex-direction: column;
          height: 100%;
          max-width: 480px;
          transform: translate(-100%, 0);
          transition: transform 0.3s ease-in-out;
          box-shadow: 20px 20px 20px 0 #00000021, inset 0 10px 20px 0 #00000021;
          padding: 30px 0 30px;
          background: #305a83;
          overflow: auto;

          .menu__item {
            position: relative;
            color: white;
            text-transform: uppercase;
            font-size: 30px;
            padding: 15px 0 15px 30px;
            cursor: pointer;
            transition: all 0.3s ease-in-out;

            &:after {
              content: "\276F";
              position: absolute;
              height: 100%;
              width: 60px;
              right: 0;
              top: 0;
              color: black;
              text-align: center;
              background: white;
              transform: translate(150%, 0);
              transition: all 0.3s ease-in-out;

              display: flex;
              align-items: center;
              justify-content: center;
            }

            &:hover {
              background: #35689a;

              &:after {
                transition: all 0.3s ease-in-out;
                transform: translate(0, 0);
              }
            }
          }
        }
      }
    }
  }

  /*========Main Container=========*/
  .main {
    max-width: 100%;
    width: 100%;
    flex: 1 1 auto;
    min-height: 1100px;
    background: #f7f8f8;
    //overflow: hidden;

    .main__container {
      max-width: 100%;
      min-width: 100%;
      display: grid;
      grid-template-columns: minmax(280px, 320px) minmax(800px, auto) minmax(100px, 320px);
      grid-gap: 30px;
      padding: 115px 30px 30px;
      position: relative;

      //Main bar with ticker
      .main__bar {
        background: #305a83;
        grid-column: span 3;
        height: 67px;
        margin: 0 -30px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        color: white;
        font-size: 20px;
        z-index: 95;
        gap: 30px;

        .bar-item {
          &.time {
            word-spacing: 5px;
          }
        }
      }

      //Column for widgets
      .left__column {
        display: grid;
        grid-template-rows: min-content;
        grid-gap: 30px;
        width: 100%;
        font-size: 15px;

        &-container {
          position: -webkit-sticky;
          position: sticky;
          top: 0px;
        }

        .exchange__widget {
          max-width: 100%;
          min-width: 280px;
          padding: 15px 20px;
          border-radius: 20px;
          background: #fff;
          height: fit-content;
          border: 1px solid #f4f3f3;

          .widget__header {
            display: flex;
            justify-content: space-between;
            padding: 0 15px 0 15px;
            color: #757575;
          }

          .widget__item {
            margin: 15px 0;
            border-radius: 20px;
            background: #f7f8f8;
            font-size: 20px;
            padding: 20px 20px;
            display: flex;
            justify-content: flex-start;
            cursor: default;
            transition: all 0.5s ease-in-out;

            &:hover {
              &.green {
                box-shadow: inset 0 0 20px #e0ffdf;
              }

              &.red {
                box-shadow: inset 0 0 20px #ffe7e7;
              }
            }

            .item__flag {
              width: 25px;
              margin: 0 5px 0 0;
            }

            .item__name {
              flex: 1 1 auto;
            }

            .item__price {
              justify-self: flex-end;
              margin-right: 10px;
            }

            .item__way {
              opacity: 0;

              &.green {
                opacity: 1;
                transform: rotate(-45deg);
                color: #27ba27;
              }

              &.red {
                opacity: 1;
                transform: rotate(45deg);
                color: #da4141;
              }

              &-arrow {
                -webkit-text-stroke: 1px white;
              }
            }
          }

          .widget__form {
            gap: 5px;
            border-radius: 20px;
            padding: 20px 10px;
            max-width: 100%;
            display: flex;
            flex-direction: column;
            font-size: 20px;
            justify-content: center;

            .form-item {
              width: 100%;
              display: flex;
              flex-direction: row;
              flex-wrap: nowrap;
              align-items: center;

              .item-select-container {
                margin-right: 10px;
                font-size: 20px;

                .item-select__control {
                  border: 0;
                  border-color: #305a83 !important;
                  box-shadow: #305a83 !important;
                  width: 100px;

                  &.item-select__control--is-focused {
                    border-color: #305a83 !important;
                    //box-shadow: 0 0 0 1px #c3c3c3 !important;
                    box-shadow: none;
                  }

                  .item-select__value-container {
                    padding: 2px;

                    .item-select__single-value {
                      display: flex;
                      flex-direction: row;

                      .label-flag {
                        max-width: 20px;
                        margin-right: 5px;
                      }
                    }
                  }

                  .item-select__indicators {
                    .item-select__indicator {
                      padding: 8px 0 !important;
                    }
                  }
                }

                .item-select__menu {

                  .item-select__menu-list {
                    height: 150px;

                  }

                  .item-select__option {
                    display: flex;
                    overflow: hidden;

                    .label-flag {
                      min-width: 20px;
                      max-width: 20px;
                      margin-right: 5px;
                    }

                    &--is-selected {
                      background: #35689a !important;
                    }

                    &--is-focused {
                      background: rgba(53, 104, 154, 0.1);
                    }
                  }
                }
              }

              .widget__form-input {
                border-bottom: 1px solid #c3c3c3;
                overflow: hidden;
                flex: 1 1 auto;
                font-size: 20px;
                width: 100%;
              }

              span {
                overflow: hidden;
                flex: 1 1 auto;
                border-bottom: 1px solid #c3c3c3;;
                width: 100%;
              }

            }

            .converter-button {
              margin: 5px 0;
              align-self: center;
              cursor: pointer;
            }
          }
        }
      }

      //Column for news
      .news__column {
        width: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: minmax(0, 1fr);

        .news__card {
          max-width: 1200px;
          width: 100%;
          margin: 0 auto;
          border-left: 1px solid #f4f3f3;
          border-right: 1px solid #f4f3f3;
          background: #ffffff;
          overflow: hidden;
          display: grid;
          grid-column: span 2;
          color: black;
          transform: translate(0, 0);
          transition: 0.15s all ease-in-out;
          grid-template-columns: auto 200px;
          padding: 30px;
          row-gap: 30px;
          column-gap: 30px;
          position: relative;

          &:hover {
            &:after {
              transform: translate(-50%, -5px);
            }

            .card__image {
              transform: translate(0, -5px);

              img {
                &:last-of-type {
                  opacity: 0.5;
                }
              }
            }

            .card__title {
              transform: translate(0, -5px);
            }

            .card__description {
              transform: translate(0, -5px);
            }

          }

          .card__image {
            grid-column: 2;
            grid-row: 1/4;
            height: 200px;
            position: relative;
            transition: 0.15s all ease-in-out;

            .preloader {
              position: absolute;
              top: 0;
              left: 0;
              height: 100%;
              width: 100%;
              background: white;
              opacity: 1;
              visibility: visible;
              transition: 0.3s all;

              .loader {
                width: 75px;
                height: 75px;
                border: 6px solid #c3c3c3;
                border-radius: 50%;
                border-top-color: #305a83;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                animation: 1s spin infinite linear;
              }

              &.done {
                opacity: 0;
                visibility: hidden;
              }

              @keyframes spin {
                from {
                  transform: translate(-50%, -50%) rotate(0deg);
                }
                to {
                  transform: translate(-50%, -50%) rotate(360deg);
                }
              }
            }

            img {
              border-radius: 20px;
              width: 100%;
              height: 100%;

              &:first-of-type {
                object-fit: cover;
              }

              &:last-of-type {
                transition: 0.15s all ease-in-out;
                position: absolute;
                top: 50%;
                width: 95%;
                height: 95%;
                border-radius: 20px;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: -1;
                filter: blur(15px) saturate(5);
                opacity: 0;
              }
            }
          }

          .card__title {
            font-weight: 600;
            text-transform: uppercase;
            font-size: calc(18px + 8 * (100vw / 1920));
            line-height: 1.1;
            transition: 0.15s all ease-in-out;
          }

          .card__description {
            font-size: calc(15px + 5 * (100vw / 1920));
            letter-spacing: 0.4px;
            line-height: 1.1;
            color: #757575;
            width: 100%;
            transition: 0.15s all ease-in-out;

            span {
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-line-clamp: 3;
              -webkit-box-orient: vertical;
            }
          }

          .card__footer {
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            gap: 10px;
            color: #757575;
            transition: 0.15s all ease-in-out;

            span {
              white-space: nowrap;
            }
          }

          &:nth-child(5n+2) /*, &:nth-child(5n+3)*/
          , &:nth-child(5n+1) {
            border-radius: 20px;
            margin-bottom: 30px;
            border: 1px solid #f4f3f3;

            &:after {
              content: none;
            }

            &:hover {
              box-shadow: 0 0 20px 0 rgba(217, 217, 217, 0.51);
              transform: translate(0, -5px);

              .card__title {
                transform: translate(0, 0);
              }

              .card__description {
                transform: translate(0, 0);
              }

              .card__image {
                transform: translate(0, 0);
              }
            }
          }

          &:nth-child(5n+2) /*, &:nth-child(5n+3) */
          {
            overflow: visible;
            grid-template-columns: 1fr 1fr;
            padding: 0 30px 0 0;
            column-gap: 30px;

            .card__image {
              grid-column: 1;
              grid-row: span 3;
              height: auto;
              min-height: 200px;
              max-height: none;
              max-width: 100%;


              img {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
              }
            }

            .card__title {
              margin-top: 30px;
              grid-column: 2;
            }

            .card__description {
              grid-column: 2;
            }

            .card__footer {
              margin-bottom: 30px;
              grid-column: 2;
            }
          }

          &:nth-child(5n+1) {
            border-radius: 20px;
            grid-column: span 2;
            padding: 0 0 30px 0;
            grid-template-rows: auto auto 1fr auto;
            grid-auto-rows: min-content;

            .card__image {
              grid-row: 1;
              width: 100%;
              max-width: 100%;
              min-height: 300px;
              height: auto;
              grid-column: span 2;

              img {
                border-radius: 0;
              }
            }

            .card__title {
              padding: 0 30px 0;
              grid-column: span 2;
            }

            .card__description {
              padding: 0 30px 0;
              grid-column: span 2;
            }

            .card__footer {
              padding: 0 30px 0;
              grid-column: span 2;
            }
          }

          &:nth-child(5n+3) {
            border-top: 1px solid #f4f3f3;
            border-top-right-radius: 20px;
            border-top-left-radius: 20px;
          }

          &:nth-child(5n) {
            border-bottom: 1px solid #f4f3f3;
            border-bottom-right-radius: 20px;
            border-bottom-left-radius: 20px;
            margin-bottom: 30px;

            &:after {
              content: none;
            }
          }

          &:last-child {
            margin-bottom: 0;
          }

          &:after {
            content: "";
            position: absolute;
            height: 1px;
            width: 90%;
            padding: 0 30px;
            left: 50%;
            bottom: 0;
            transform: translate(-50%, 0);
            background: #f4f3f3;
            transition: 0.15s all ease-in-out;
          }

        }


        .news__message {
          grid-column: span 2;
          width: 100%;
          font-size: 40px;
          margin: 30px 0;
          text-align: center;
          color: #9c9c9c;
          line-height: 1.5;

          svg {
            width: 80px;
            height: 80px;
          }
        }
      }
    }
  }

  /*========Footer=========*/
  .footer {
    background: #305a83;

    .footer__container {
      padding: 30px 30px;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;

      .footer__column {
        padding: 0 30px;
        border-right: 2px solid white;
      }

      .footer__logo {
        display: flex;
        font-size: 60px;
        color: white;
        text-align: center;

        .logo-item {
          align-self: center;
          color: white;
        }
      }

      .footer__resources {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        grid-gap: 20px;
        font-size: 30px;

        .resources__title {
          white-space: nowrap;
          line-height: 38px;
          padding: 0 0 3px;
          border-bottom: 2px solid white;
          color: white;
        }

        .resources__item {
          display: inline-block;
          min-height: 46px;
          max-width: 200px;

          img {
            height: 40px;
          }
        }

      }

      .footer__contact {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        font-size: 30px;
        gap: 20px;

        .contact__title {
          line-height: 38px;
          padding: 0 0 3px;
          border-bottom: 2px solid white;
          color: white;
        }

        .contacts__container {
          display: flex;
          flex-direction: row;
          flex-wrap: wrap;
          font-size: 35px;
          width: 100%;
          height: auto;
          gap: 20px;

          .contact-item {
            color: white;
            transition: transform 0.3s ease-out;

            &:hover {
              transform: scale(1.1) translate(0, 0);
            }

            &.email__item {
              flex-grow: 1;
              font-size: 20px;
              width: 100%;

              &:hover {
                text-decoration: underline;
                transform: scale(1) translate(0, 0);
              }
            }
          }
        }
      }
    }
  }
}

/*========Adaptive=========*/
@media screen and (max-width: 1330px) {
  .wrapper {
    overflow: hidden;

    .header {
      .header__container {
        .header__navigation {
          padding: 30px 20px 30px 20px;

          .header__menu {
            gap: 20px;
          }
        }
      }
    }

    .main {
      .main__container {
        padding: 115px 20px 30px;
        grid-gap: 20px;
        grid-template-columns: 280px minmax(800px, auto);

        .main__bar {
          grid-column: span 2;
        }

        .left__column {
          grid-gap: 30px;
        }

        .news__column {
          .news__card {
            &:nth-child(5n+2) /*, &:nth-child(10n+3),*/
            , &:nth-child(5n+1) {
              margin-bottom: 20px;
            }

            &:nth-child(5n) {
              margin-bottom: 20px;
            }
          }
        }
      }
    }

    .footer {
      .footer__container {
        padding: 30px 20px;

        .footer__column {
          padding: 0 20px;
        }
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .wrapper {
    .header {
      .header__container {
        .header__navigation {
          .header__menu {
            .menu__item {
              &.search-news {
                .search-news__form {
                  width: 100%;
                }
              }
            }
          }
        }
      }
    }

    .main {
      .main__container {
        grid-template-columns: 1fr;

        .main__bar {
          grid-column: 1;
        }

        .left__column {
          order: 2;
          grid-template-columns: 1fr 1fr;
          grid-gap: 20px;
        }

        .news__column {
          .news__card {

            .card__information {
              width: 100%;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1000px) {
  .wrapper {
    .header {
      .header__container {
        .header__navigation {
          .header__menu {
            .menu__item {
              &.load-default {
                display: none;
              }
            }
          }
        }
      }
    }

    .footer {
      .footer__container {
        grid-template-columns: 1fr 1fr;

        .footer__logo {
          display: none;
        }

        .footer__resources {
          border-left: 2px solid white;
        }
      }
    }
  }
}

@media screen and (max-width: 850px) {
  input::placeholder {
    color: white;
  }
  .wrapper {
    .main {
      .main__container {
        .news__column {
          .news__card {
            &:nth-child(5n+2) /*, &:nth-child(5n+3)*/
            {
              grid-column: span 2;

              .card__image {
                grid-row: span 2;
              }

              .card__description {
                display: none;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 767.98px) {
  .wrapper {
    .main {
      .main__container {
        .left__column {
          grid-template-columns: 1fr;
        }

        .news__column {
          .news__card {
            grid-template-columns: auto 150px;

            .card__image {
              height: 150px;
              grid-row: 1;
            }

            .card__description {
              grid-column: span 2;
            }

            .card__footer {
              grid-column: span 2;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .wrapper {
    .header {
      .header__container {
        .header__navigation {
          padding: 20px 10px 10px;
          max-height: 75px;
          overflow: hidden;
          transition: all 0.2s ease-in-out;

          &.open__mobile-search {
            transition: all 0.5s ease-in-out;
            max-height: 100%;

            .header__menu {
              .menu__item {
                &.search-news {
                  opacity: 1;
                  transition: all 0.3s ease-in-out 0.3s;
                }
              }
            }
          }

          .header__menu {
            flex-wrap: wrap;

            .menu__item {
              &.title {
                font-size: 35px;
              }

              &.chose-category {
                width: 40px;
                flex: 1 1 auto;

                span {
                  left: 0;
                  transform: translate(0, -50%);
                }
              }

              &.change-country {
                padding: 0 0 0 5px;

                .change-country__select {
                  width: 20px;
                }
              }

              &.mobile-search-news {
                display: block;
                transition: 0.3s all ease-in-out;

                &.non-active {
                  transform: scale(0.9);
                  color: #f4f3f3;
                }
              }

              &.search-news {
                min-width: 100%;
                width: 100%;
                order: 4;
                opacity: 0;
                transition: all 0.2s ease-in-out;
              }
            }
          }
        }

        .header__category {
          top: 75px;

          .category__menu {
            width: 320px;
          }
        }
      }
    }

    .main {
      .main__container {
        padding: 75px 20px 20px;

        .news__column {
          .news__card {
            padding: 20px;
            grid-gap: 20px;

            &:nth-child(10n+1) {
              padding: 0 0 20px 0;

              .card__title {
                padding: 0 20px;
              }

              .card__description {
                padding: 0 20px;
              }

              .card__footer {
                padding: 0 20px;
              }
            }

            &:nth-child(5n+2) /*, &:nth-child(5n+3)*/
            {
              grid-column: span 2;
              padding: 0 0 20px 0;
              border-radius: 20px;
              overflow: hidden;
              grid-gap: 20px;

              .card__image {
                grid-column: span 2;
                grid-row: 1;

                img {
                  border-radius: 0;
                }
              }

              .card__title {
                margin: 0;
                grid-column: span 2;
                padding: 0 20px;

              }

              .card__footer {
                margin: 0;
                grid-column: span 2;
                padding: 0 20px;
              }
            }
          }
        }
      }
    }

    .footer {
      .footer__container {
        padding: 20px 10px;
        grid-template-columns: 1fr;
        grid-row-gap: 20px;

        .footer__column {
          padding: 20px 10px;
          border: 0;
          border-bottom: 2px solid white;
        }
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .wrapper {

    .main {
      .main__container {
        padding: 75px 0 20px;

        .news__column {
          .news__card {
            border-left: 0;
            border-right: 0;

            &:nth-child(5n+2) /*, &:nth-child(10n+3)*/
            , &:nth-child(5n+1) {
              border-left: 0;
              border-right: 0;
            }

            padding: 20px;
            grid-gap: 20px;
            grid-template-columns: auto 90px;


            .card__image {
              height: 90px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 350px) {
  .wrapper {
    .main {
      .main__container {
        .news__column {
          .news__card {
            padding: 10px;
            grid-gap: 10px;
          }
        }
      }
    }
  }
}